import {
  React,
  connect,
  Component,
  reduxForm,
  Field,
  InputDate,
  Tabel,
  Loading,
  getTglSystem,
  ReanderSelect,
} from "../../../../components/helpers/library";
import CetakLaporanRekap from "./pdf/CetakLaporanRekap";
import CetakLaporanKeuanganDetail from "./pdf/CetakLaporanKeuanganDetail";
import LaporanKeaunagnRekap from "./exel/LaporanKeaunagnRekap";
import LaporanKeuanganDetail from "./exel/LaporanKeuanganDetail";
class HeadFormNonCash extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      columnsdetail: [
        {
          dataField: "kategori",
          text: "Kategori",
          formatter: (data) => {
            return data === undefined ? "-" : data;
          },
          csvFormatter: (data) => {
            return data === undefined ? "-" : data;
          },
          footerAttrs: {
            colSpan: "3",
          },
          footer: "Total",
        },
        {
          dataField: "deskripsi",
          text: "Deskripsi",
          formatter: (data) => {
            // console.log(data);
            let row = data || "-";
            let hasil = row.split("<br/>");
            return data === undefined ? (
              "-"
            ) : (
              <>
                {" "}
                {hasil[1]} <br /> {hasil[2]}{" "}
              </>
            );
          },
          csvFormatter: (data) => {
            return data === undefined ? "-" : data;
          },
        },
        {
          dataField: "fee",
          text: "Fee",
          headerClasses: "text-right",
          formatter: (data) => {
            return <div className="text-right"> {data === undefined ? "-" : data + '%'} </div>;
          },
        },
        
        {
          dataField: "jumlah_in",
          text: "Uang Masuk",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right">
                {" "}
                {data === undefined || null
                  ? 0
                  : parseFloat(data).toLocaleString("kr-KO")}
              </div>
            );
          },
          csvFormatter: (data) => {
            return (
              <div className="text-right">
                {" "}
                {data === undefined || null ? "0" : data}{" "}
              </div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {parseInt(
                columnData.reduce((acc, item) => acc + item, 0)
              ).toLocaleString("kr-KO")}{" "}
            </div>
          ),
        },
        {
          dataField: "jumlah_in_fee",
          text: "Uang Masuk + Fee",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right">
                {" "}
                {data === undefined || null
                  ? 0
                  : parseFloat(data).toLocaleString("kr-KO")}
              </div>
            );
          },
          csvFormatter: (data) => {
            return (
              <div className="text-right">
                {" "}
                {data === undefined || null ? "0" : data}{" "}
              </div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {parseInt(
                columnData.reduce((acc, item) => acc + item, 0)
              ).toLocaleString("kr-KO")}{" "}
            </div>
          ),
        },
      ],
      columnsrekap: [
        {
          dataField: "kategori",
          text: "Kategori",
          footer: "Total",
          formatter: (data) => {
            return data === undefined ? "-" : data;
          },
        },
        {
          dataField: "jumlah_in",
          text: "Uang Masuk",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right">
                {" "}
                {data === undefined || null
                  ? 0
                  : parseFloat(data).toLocaleString("kr-KO")}
              </div>
            );
          },
          csvFormatter: (data) => {
            return (
              <div className="text-right">
                {" "}
                {data === undefined || null ? "0" : data}{" "}
              </div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {parseInt(
                columnData.reduce((acc, item) => acc + item, 0)
              ).toLocaleString("kr-KO")}{" "}
            </div>
          ),
        },
        {
          dataField: "jumlah_in_fee",
          text: "Uang Masuk + Fee",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right">
                {" "}
                {data === undefined || null
                  ? 0
                  : parseFloat(data).toLocaleString("kr-KO")}
              </div>
            );
          },
          csvFormatter: (data) => {
            return (
              <div className="text-right">
                {" "}
                {data === undefined || null ? "0" : data}{" "}
              </div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {parseInt(
                columnData.reduce((acc, item) => acc + item, 0)
              ).toLocaleString("kr-KO")}{" "}
            </div>
          ),
        },
      ],
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdfRekap() {
    CetakLaporanRekap(this.props.datalaporancash);
  }
  exportPdfDetail() {
    CetakLaporanKeuanganDetail(this.props.datalaporancash);
  }

  componentDidMount() {
    this.props.change("type", "REKAP");
    this.props.change("filter", "TRANSFER");
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="type"
              component={ReanderSelect}
              onChange={(e) => this.props.onChange(e)}
              options={[
                { value: "DETAIL", name: "DETAIL" },
                { value: "REKAP", name: "REKAP" },
              ]}
              label="Type"
              placeholder="Pilih Type"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="filter"
              component={ReanderSelect}
              onChange={(e) => this.props.onChange(e)}
              options={[
                { value: "TRANSFER", name: "TRANSFER" },
                { value: "DEBET", name: "DEBET" },
                { value: "CREDIT", name: "KREDIT" },
              ]}
              label="Filter"
              placeholder="Filter Type Pembayaran"
            />
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
         
          <div className="col-lg-12">
            <Tabel
              keyField="kategori"
              data={this.props.datalaporancash}
              columns={
                this.props.istypeRekap
                  ? this.state.columnsrekap
                  : this.state.columnsdetail
              }
              empty={true}
              textEmpty="Data Keuangan Non Cash Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            <label htmlFor="">&nbsp;</label>
            <div className="row">
              <div className="col-lg-6">
                {this.props.istypeRekap ? (
                  <button
                    onClick={() => this.exportPdfRekap()}
                    className="btn btn-warning btn-block"
                  >
                    {" "}
                    Export Rekap PDF{" "}
                  </button>
                ) : (
                  <button
                    onClick={() => this.exportPdfDetail()}
                    className="btn btn-warning btn-block"
                  >
                    {" "}
                    Export Detail PDF{" "}
                  </button>
                )}
              </div>
              <div className="col-lg-6">
                {this.props.istypeRekap ? (
                  <LaporanKeaunagnRekap data={this.props.datalaporancash} />
                  ) : (
                  <LaporanKeuanganDetail data={this.props.datalaporancash} />
                  )}
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadFormNonCash = reduxForm({
  form: "HeadFormNonCash",
  enableReinitialize: true,
})(HeadFormNonCash);
// export default connect()(HeadFormNonCash);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
    },
  };
})(HeadFormNonCash);
