import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  getData,
  formatDate,
  moment,
  localStorageencryp,
  ToastNotification,
} from "../../../../components/helpers/library";
import HeadFormNonCash from "./HeadFormNonCash";

class LaporanNonCash extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      hidden: false,
      istypeRekap: true,
      tanggal: false,
      datalaporancash: [],
      datamember: [],
      export: false
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    this.setState({
      isLoading: true,
    })
    let data_post = {
      tanggal : formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
    };
    getData(`report-non-cash?type=${data.type}&tanggal=${data_post.tanggal}&jenis=${data.filter}`).then((res) => {
      if (res.data.length === 0) {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        this.setState({
          isLoading: false,
        })
      } else {
        ToastNotification("success", "Laporan Keuangan Non Cash Tersedia");
        // console.log(res.data);
        this.setState({
          datalaporancash: res.data,
          isLoading: false,
          export: true
        })
      }
      localStorageencryp('tgl_laporan',JSON.stringify(data_post))

    }).catch((err) => {
      ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
      this.setState({
        isLoading: false,
        export : false
        
      })
      console.log(err);
    })
  }
  onChange(e) {
    if (e.type === "REKAP") {
      this.setState({ istypeRekap: true,datalaporancash : [] });
    } else {
      this.setState({ istypeRekap: false,datalaporancash : [] });
    }
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan Keuangan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Keuangan Non Cash</li>
        </ol>
        <h1 className="page-header">Laporan Keuangan Non Cash </h1>
        <Panel>
          <PanelHeader>Laporan Keuangan Non Cash</PanelHeader>
          <br />
          {/* Laporan Keuangan Non Cash */}
          <div className="container">
            <HeadFormNonCash
              onChange={(data) => this.onChange(data)}
              isLoading={this.state.isLoading}
              export={this.state.export}
              datalaporancash={this.state.datalaporancash}
              istypeRekap={this.state.istypeRekap}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </div>
          <br />
          {/* End Tambah Laporan Keuangan Non Cash  */}
        </Panel>
      </div>
    );
  }
}

LaporanNonCash = reduxForm({
  form: "LaporanNonCash",
  enableReinitialize: true,
})(LaporanNonCash);
export default connect()(LaporanNonCash);
